* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#back-color {
  background-color: #00203FFF;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s linear ;
} 

#quote-box {
  position: relative;
  background-color: #ADEFD1FF;
  color: #00203FFF;
  width: 550px;
  transition: all 0.5s linear ;
}

#icon {
  font-size: 40px;
  margin: 10px;
  text-align: center;
  transition: color 0.5s linear;
}

#tweet-quote, #tumblr {
  position: absolute;
  top: 10px;
  font-size: 20px;
  margin: 5px;
  color: #00203FFF;
  transition: color 0.5s linear ;
}

#tweet-quote {
  right: 10px;
}

#tumblr {
  right: 30px;
}

#tweet-quote:hover, #tumblr:hover{
  opacity: 0.8;
}

#text {
  display: block;
  width: 90%;
  margin: 0 5% 0 5%;
  text-align: center;
  font-size: 30px;
  font-weight: 800;
  transition: color 0.5s linear ;
}

#author {
  display: block;
  width: 100%;
  text-align: center;
  padding: 10px 0 30px 0;
}

#btns-container {
  width: 100%;
  padding-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#btns-container button{
  border: none;
  margin: 0 5px;
  background-color: #00203FFF;
  color: #ADEFD1FF;
  font-weight: 200;
  font-size: 12px;
  width: 120px;
  height: 30px;
  border-radius: 2px;
  cursor: pointer;
  transition: background-color 0.5s linear, color 0.5s linear, opacity 0.2s linear ;
}

#btns-container button:hover {
  opacity: 0.8;
}
